import { Component } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { LocalStorageServiceService } from 'src/service/local-storage-service.service';
import { GetDataBackendService } from '../../service/get-data-backend.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss']
})
export class homePage {

  DatosUser: any
  DataAsignacion: any = []
  AsignacionD: any = []
  PosArray: number = 0
  ListUsuarios: any = []

  constructor(
    private GetConsulta: GetDataBackendService,
    private _localstorage: LocalStorageServiceService,
    private alertController: AlertController
  ) { }

  ngOnInit() {
    this.DatosUser = {}
    //asigno los datos del usuario que tengo en el local storage 
    this.DatosUser = JSON.parse(this._localstorage.BuscarItem('DatosUser'))
    this.GetListUser()
  }

  ionViewDidEnter() {
    this.DatosUser = {}
    //asigno los datos del usuario que tengo en el local storage 
    this.DatosUser = JSON.parse(this._localstorage.BuscarItem('DatosUser'))
    this.GetListUser()
  }


  //obtengo los usuarios del barbero
  GetListUser() {
    try {

      this.DataAsignacion = []
      this.ListUsuarios = []

      if (this.DatosUser.TipoUser === 'Barbero') {
        this.GetConsulta.GetClientesBarbero(this.DatosUser.idUser).then((ResUser: any) => {
          if (ResUser.length > 0) {
            this.ListUsuarios = ResUser
          }
        }).catch((error: any) => console.error(error))
      }

      this.GetAsignacion()

    } catch (error) {
      console.error(error)
    }
  }

  //obtengo las aignaiones del dia actual 
  GetAsignacion() {
    try {

      //obtengo el dia de hoy 
      const Fecha = new Date()

      switch (this.DatosUser.TipoUser) {
        case 'Barbero':
          //busco los datos de las asignaciones del dia de hoy en la base de datos 
          this.GetConsulta.GetAgendaByDate(this.DatosUser.idUser, this.DatosUser.TipoUser, `${Fecha.getDate()}-${Fecha.getMonth() + 1}-${Fecha.getFullYear()}`).then((ResData: any) => {

            if (ResData.length > 0) {

              if (this.ListUsuarios.length > 0) {
                //busnco en todos los servicios del dia el nombre de cada cliebte
                for (let index = 0; index < ResData.length; index++) {

                  let user = this.ListUsuarios.find((U: any) => U.idUser === ResData[index].idUser)
                  if (user) {
                    //Guardo los datos de las citas del dia en un arreglo
                    this.DataAsignacion.push({
                      "NameClient": user.Name,
                      "idAgenda": ResData[index].idAgenda,
                      "idUser": ResData[index].idUser,
                      "idBarbero": ResData[index].idBarbero,
                      "Cantidad": ResData[index].Cantidad,
                      "Servicios": ResData[index].Servicios.join().replace(",", ", "),
                      "Dia": ResData[index].Dia,
                      "Fecha": ResData[index].Fecha,
                      "Hora": ResData[index].Hora,
                      "Monto": ResData[index].Monto,
                      "Status": ResData[index].Status,
                      "FechaCreate": ResData[index].FechaCreate
                    })

                  }

                }

              } else { this.DataAsignacion = ResData }

            }

          }).catch(err => console.error(err))
          break;

        case 'Cliente':
          //busco los datos de las asignaciones del dia de hoy en la base de datos 
          this.GetConsulta.GetHistorial(this.DatosUser.idUser, this.DatosUser.TipoUser).then((ResData: any) => {

            if (ResData.length > 0) {
              //Volteo el arreglo para mostrar primero lo ultimo que se agendo 
              ResData = ResData.reverse()

              for (let index = 0; index < ResData.length; index++) {
                //Guardo los datos de las citas del dia en un arreglo
                this.DataAsignacion.push({
                  "NameClient": "",
                  "idAgenda": ResData[index].idAgenda,
                  "idUser": ResData[index].idUser,
                  "idBarbero": ResData[index].idBarbero,
                  "Cantidad": ResData[index].Cantidad,
                  "Servicios": ResData[index].Servicios.join().replace(",", ", "),
                  "Dia": ResData[index].Dia,
                  "Fecha": ResData[index].Fecha,
                  "Hora": ResData[index].Hora,
                  "Monto": ResData[index].Monto,
                  "Status": ResData[index].Status,
                  "FechaCreate": ResData[index].FechaCreate
                })

              }

            }

          }).catch(err => console.error(err))
          break;

      }

    } catch (error) {
      console.error(error)
    }
  }

  NextAgenda() {
    //muestro la siguiente cita 
    if (this.PosArray <= (this.DataAsignacion.length - 1)) {
      this.PosArray = this.PosArray + 1
    }
  }

  PreviousAgenda() {
    //muestro la cita anterior 
    if (this.PosArray > 0) {
      this.PosArray = this.PosArray - 1
    }
  }

  async ChangeStatus(DataAgenda: any, NewStatus?: any) {
    try {
      //Cambio el estatus de la cita
      if (NewStatus) {

        const alert = await this.alertController.create({
          header: '¿Usted desea cancelar su cita?',
          buttons: [
            {
              text: 'Cancelar',
              role: 'cancel',
              cssClass: 'secondary',
            },
            {
              text: 'Confirmar',
              handler: async (data) => {
                DataAgenda.Status = NewStatus
                this.SaveNewStatusAgenda(DataAgenda)
              }
            }
          ]
        });

        await alert.present();

      } else {
        const alert = await this.alertController.create({
          header: 'Seleccione un Status',
          inputs: [
            {
              label: 'Atendido',
              type: 'radio',
              value: 'Atendido'
            },
            {
              label: 'Cancelado',
              type: 'radio',
              value: 'Cancelado'
            }
          ],
          buttons: [
            {
              text: 'Confirmar',
              handler: async (data) => {
                DataAgenda.Status = data
                this.SaveNewStatusAgenda(DataAgenda)
              }
            }
          ]
        });

        await alert.present();
      }

    } catch (error) {
      console.error(error)
    }
  }

  SaveNewStatusAgenda(Data: any) {
    try {
      //Guardo el nuevo status de la cita 
      this.GetConsulta.UpdateAgenda(Data).then(async (ResUpdate: any) => {

        if (ResUpdate.status === true) {

          const alert = await this.alertController.create({
            header: 'El status se actualizo con éxito',
            buttons: ['Aceptar']
          });

          await alert.present();

          this.DataAsignacion[this.PosArray] = Data

        } else {

          const alert = await this.alertController.create({
            header: 'No se logro actualizar el status',
            buttons: ['Aceptar']
          });

          await alert.present();

        }

      }).catch(async (error: any) => {
        const alert = await this.alertController.create({
          header: 'No se logro actualizar el status',
          buttons: ['Aceptar']
        });

        await alert.present();

        console.error(error)
      })

    } catch (error) {
      console.error(error)
    }
  }

}
